.menu {
  $this: &;
  visibility: hidden;
  opacity: 0;
  background-color: $white;
  position: fixed;
  z-index: 20;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  @include tr(opacity .3s, visibility .3s);
  &.is-active {
    visibility: visible;
    opacity: 1;
    #{$this}__inner {
 } }      // transform: translate(0, 0)
  &__inner {
    width: percentage(1720/1920);
    height: 100%;
    padding: 40px;
    background-color: $black;
    transform: translateX(-100%);
    @include tr(transform .3s);
    @include md {
      padding: 30px; }
    @include sm {
      padding: 20px; }
    @include xs {
      padding: 10px; } }
  &__block {
    @include cover;
    height: 100%;
    padding: 25px;
    position: relative;
    @include flex(flex-start, center);
    @include sm {
      padding: 15px; }
    @include xs {
      padding: 10px; } }
  &__logo {
    position: absolute;
    z-index: 1;
    top: 25px;
    left: 25px; }
  &__nav {
    margin-left: percentage(265/1620);
    @include xs {
      margin-left: 0; } }
  &__close {
    color: $black;
    @include icon-close(30, 1, currentColor);
    position: absolute;
    z-index: 1;
    top: 75px;
    right: percentage(100/1920);
    transform: translate(50%, 0);
    @include tr(color .3s);
    @include hover {
      color: $accent; }
    @include md {
      top: 45px; }
    @include sm {
      top: 30px;
      width: 25px;
      height: 25px; }
    @include xs {
      top: 20px;
      width: 20px;
      height: 20px; } } }
