.home {
  height: 100vh;
  background-color: $black;
  background-image: linear-gradient( -180deg, rgb(36,36,36) 0%, rgb(1,1,1) 100%);
  @include image('home/bg.jpg');
  &__controls {
    position: fixed;
    bottom: 0;
    z-index: 15;
    width: percentage(960/1920);
    height: $controls-height;
    background-color: $gray-darkest;
    @include flex(space-between, center);
    @include xl {
      width: 60%; }
    @include lg {
      width: 100%; }
    @include sm {
      height: $controls-height-mob; }
    @include rh(667) {
      height: $controls-height-vert; } }
  &__btns {
    width: 180px;
    @include sm {
      width: 130px; }
    @include xs {
      width: 100px; } }
  &__prev,
  &__next {
    position: relative;
    display: inline-block;
    background-color: transparent;
    height: 45px;
    width: 45px;
    color: $white;
    border: 1px solid currentColor;
    border-radius: 50%;
    @include tr(color .3s);
    @include hover {
      color: $gray-dark; }
    @include before {
      @include icon-arrow(8, 8, 1, currentColor, 45);
      @include vcenter;
      transform: translate(-40%, -50%) rotate(45deg); }
    @include sm {
      height: 30px;
      width: 30px; } }
  &__next {
    @include rotate(180); }
  &__prev {
    margin-right: 15%; }
  &__slides-nav {
    flex-grow: 1; } }

.home-page {
  height: 100%;
  position: relative;
  padding-bottom: $controls-height;
  @include sm {
    padding-bottom: $controls-height-mob; }
  @include rh(667) {
      padding-bottom: $controls-height-vert; }
  .container {
    height: 100%;
    @include flex(center, normal, column); }
  &__title {
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 10px; } }
  &__subttl {
    margin-bottom: 120px;
    @include lg {
      margin-bottom: 90px; }
    @include md {
      margin-bottom: 80px; }
    @include sm {
      margin-bottom: 60px; }
    @include xs {
      margin-bottom: 30px; }
    @include rh(667) {
      margin-bottom: 20px; } }

  &__img {
    position: absolute;
    bottom: 0;
    top: 0px - $header-height;
    right: 0;
    z-index: 0;
    opacity: 0;
    @include md {
      top: 0px - $header-height-md; }
    @include sm {
      top: 0px - $header-height-sm; } }
  &__block {
    position: relative;
    z-index: 1;
    max-width: 50%;
    opacity: 0;
    @include md {
      max-width: 75%; }
    @include sm {
      max-width: none; } }

  &__word {
    position: absolute;
    z-index: 1;
    left: 45%;
    bottom: 20%; }
  &__word-emotions {
    left: auto;
    right: -320px;
    @include xl {
      right: -300px; }
    @include md {
      right: -240px; } }
  &__word-services {
    left: auto;
    right: -270px;
    @include xl {
      right: -250px; }
    @include md {
      right: -200px; } } }

.home-nav {
  @include flex(space-between, center);
  width: 100%;
  @include sm {
    justify-content: flex-start; }
  &__nmb {
    display: block;
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 0; } }
  &__item {
    display: block;
    color: $gray-dark;
    position: relative;
    padding-bottom: 12px;
    @include css-lock(15, 45, 1199, 1799, padding-left);
    @include css-lock(15, 45, 1199, 1799, padding-right);
    width: 33.33333%;
    @include hover {
      @extend .home-nav__item.is-active; }
    @include sm {
      width: auto; }
    &.is-active {
      color: $gray-light;
      @include before {
        visibility: visible;
        opacity: 1; } }
    @include before {
      position: absolute;
      bottom: 0;
      left: 45px;
      width: 50px;
      background-color: currentColor;
      height: 1px;
      visibility: hidden;
      opacity: 0;
      @include tr(visibility .3s, opacity .3s);
      @include css-lock(15, 45, 1199, 1799, left);
      @include sm {
        width: 25px; } }

    @include notlast {
      @include after {
        position: absolute;
        height: 30px;
        width: 1px;
        background-color: $gray-dark;
        top: .5em;
        right: 0;
        @include sm {
          display: none; } } } }
  &__text {
    @include sm {
      display: none; } } }
