.section-bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  @include cover;
  opacity: 0;
  overflow: hidden;
  video {
    @include coverdiv;
    object-fit: cover;
    &.is-desctop {
      @include sm {
        display: none; } } }
  &--mob-left {
    @include sm {
      background-position: 65% 50%; } }
  &--mob-left-lg {
    @include sm {
      background-position: 85% 50%; } } }

