body {
  background: #FFF;
  line-height: 1.4;
  font-size: $fz;
  @include light;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm; } }

input, textarea {
  font-family: inherit;
  @include placeholder {
    font-family: inherit; } }

.overflow-visible {
  overflow: visible !important; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

// [data-loaded="true"]
//   animation: fadeIn 1s

.is-desctop {
  @include sm {
    display: none; } }

.is-mob {
  @include smmin {
    display: none; } }

.is-xs-none {
  @include xs {
    display: none; } }

.is-xs-min {
  @include xsmin {
    display: none; } }

.container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  &--sm {
    @include r(1699) {
      max-width: 1200px; }
    @include xl {
      max-width: 1000px; } } }

.logo {
  $this: &;
  color: $white;
  @include extrabold;
  text-transform: uppercase;
  font-size: $fz-md;
  // max-width: 150px
  @include flex(flex-start, center);
  line-height: 0.95;
  @include xs {
    font-size: $fz;
    img {
        // max-width: 75%
        height: 28px;
        width: 28px; } }
  @include r(389) {
    font-size: $fz-sm;
    img {
      height: 22px;
      width: 22px; } }

  &__img {
    margin-right: 12px;
    @include sm {
      margin-right: 5px; }
    @include r(389) {
      margin-right: 2px; } }
  &__text {
    span {
      display: block; } }
  &--color {
    #{$this}__text {
      span {
        &:first-child {
          color: $accent; }
        &:last-child {
          color: #66656a; } } } } }

.link-arrow {
  color: $white;
  position: relative;
  @include inline-flex(flex-start, center);
  @include hover {
    .link-arrow__arrow {
      width: 70px; } }
  &__title {
    margin-right: 20px; }
  &__arrow {
    position: relative;
    width: 50px;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
    display: inline-block;
    transition: transform .3s, width .3s;
    &::before {
      width: 100%;
      content: '';
      position: absolute;
      height: 1px;
      background-color: currentColor;
      top: 50%;
      right: -1px;
      transform: translate(0, -50%);
      transition: width .3s; }
    &::after {
      display: inline-block;
      position: absolute;
      @include icon-arrow(8, 8, 1, currentColor, 225);
      content: '';
      top: 50%;
      transform: translate(0, -50%) rotate(225deg);
      right: 0; } } }

.text-stroke {
  font-size: 250px;
  line-height: .6;
  text-transform: uppercase;
  @include bold;
  -webkit-text-stroke: 3px $gray-dark;
  color: transparent;
  // +css-lock(100, 250, 320, 1199, font-size)
  @include xl {
    font-size: 220px; }
  @include lg {
    font-size: 200px; }
  @include md {
    font-size: 180px; }
  @include sm {
    display: none; } }

.purple-text {
  color: $purple;
  @include bold;
  text-shadow: 0 0 50px rgba($purple, 1), 3px 15px 2px rgba($black-darkest, .4), rgba($white, .8) 0px -1px 0px;
  position: relative;
  &::before {
    content: attr(data-text);
    text-shadow: 0 0 50px rgba($white, 1), 3px 15px 2px rgba($black-darkest, .4), rgba($white, .8) 0px -1px 0px;
    position: absolute;
    @include vcenter;
    z-index: 0; } }

.scroll-down {
  $this: &;
  color: $white;
  position: relative;
  padding-bottom: 50px;
  display: inline-block;
  @include hover {
    #{$this}__arrow {
      height: 60px; } }
  &__arrow {
    height: 50px;
    display: inline-block;
    position: absolute;
    left: 50%;
    top: 1.5em;
    transform: translate(-50%, 0);
    @include tr(height .3s);
    @include before {
      height: 100%;
      width: 1px;
      background-color: currentColor;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-50%, 0); }
    @include after {
      @include icon-arrow(10, 10, 1, currentColor, -45);
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 0) rotate(-45deg); } } }

.scroll-top {
  $this: &;
  display: inline-block;
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 30px;
  @include hover {
    #{$this}__arrow {
      height: 40px; } }
  &__arrow {
    height: 30px;
    width: 10px;
    display: inline-block;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    vertical-align: middle;
    @include tr(height .3s);

    @include before {
      height: 100%;
      background-color: currentColor;
      width: 1px;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-50%, 0); }
    @include after {
      @include icon-arrow(10, 10, 1, currentColor, 135);
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0) rotate(135deg); } } }

.toggle-btn {
  display: inline-block;
  position: relative;
  // padding-right: 20px
  font-size: $fz;
  @include bold;
  color: $white;
  margin-bottom: 15px;
  @include after {
    @include icon-arrow(8, 8, 1, currentColor, -45);
    position: relative;
    top: -.1em;
    display: inline-block;
    margin-left: 10px; }
  &.is-active {
    @include after {
      @include rotate(-225);
      top: .1em; } }
  &__wrap {
    width: 100%; } }


// ========================= animations ==============================

.glowing {
  // animation: glow 2s infinite
  &.is-animate {
    &::before {
      animation: flashIn 2.5s; } } }


.flashIn {
  animation-duration: 2s;
  &.is-animate {
    animation-name: flashIn; } }

@keyframes shadow {
  0% {
    text-shadow: none; }
  25% {
    text-shadow: #f21f4d 0px 0px 5px, #fd0000 0px 0px 10px, #FFF 0px 0px 15px, #CE0000 0px 0px 20px, #CE0000 0px 0px 30px, #CE0000 0px 0px 40px, #FF2D95 0px 0px 50px, #CE0000 0px 0px 75px, 2px 2px 2px rgba(206,0,0,0.63); }
  75% {
    text-shadow: none; }
  100% {
    text-shadow: #f21f4d 0px 0px 5px, #fd0000 0px 0px 10px, #FFF 0px 0px 15px, #CE0000 0px 0px 20px, #CE0000 0px 0px 30px, #CE0000 0px 0px 40px, #FF2D95 0px 0px 50px, #CE0000 0px 0px 75px, 2px 2px 2px rgba(206,0,0,0.63); } }

@keyframes flick {
  0% {
    transform: translateX(0); }

  5% {
    transform: translateX(5px); }

  6% {
    transform: translateX(0); }

  7% {
    transform: translate(0, 5px); }

  8% {
    transform: none; }

  14% {
    transform: translateX(-5px); }

  15% {
    transform: none; }

  100% {
    transform: none; } }


@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes flashIn {
  0% {
    opacity: 1; }
  25% {
    opacity: 0.1; }
  50% {
    opacity: 1; }
  75% {
    opacity: 0.1; }
  100% {
    opacity: 1; }

