@include font('Circe-Regular', 'Circe-Regular');
@include font('Circe-Light', 'Circe-Light');
@include font('Circe-Thin', 'Circe-Thin');
@include font('Circe-ExtraLigh', 'Circe-ExtraLigh');
@include font('Circe-Bold', 'Circe-Bold');
@include font('Circe-ExtraBold', 'Circe-ExtraBold');

@mixin reg {
  font-family: 'Circe-Regular', Arial, sans-serif; }
@mixin light {
  font-family: 'Circe-Light', Arial, sans-serif; }
@mixin extralight {
  font-family: 'Circe-Thin', Arial, sans-serif; }
@mixin thin {
  font-family: 'Circe-ExtraLigh', Arial, sans-serif; }
@mixin bold {
  font-family: 'Circe-Bold', Arial, sans-serif; }
@mixin extrabold {
  font-family: 'Circe-ExtraBold', Arial, sans-serif; }

