.header {
  position: fixed;
  z-index: 15;
  left: 0;
  right: 0;
  top: 0;
  background-color: transparent;
  height: $header-height;
  padding-left: $padding;
  padding-right: $padding;
  @include flex(space-between, center);
  @include xl {
    padding-left: $padding-sm;
    padding-right: $padding-sm; }
  @include md {
    height: $header-height-md;
    padding-left: $padding-md; }
  @include sm {
    height: $header-height-sm; }
  @include r(389) {
    // padding-left: 10px
 }    // padding-right: 10px
  @include rh(900) {
    height: $header-height-sm-vert; }
  &__nav {
    flex-grow: 1;
    text-align: right; } }

.burger {
  $gap: 4px;
  $gap-sm: 2px;
  display: block;
  color: $white;
  width: 38px;
  // height: 33px
  padding-bottom: $gap;
  padding-top: $gap;
  position: relative;
  @include tr(color .3s);
  @include hover {
    color: $gray-dark; }
  @include sm {
    width: 30px;
    padding-top: $gap-sm;
    padding-bottom: $gap-sm; }
  @include r(389) {
    width: 20px; }
  &::before,
  &::after {
    content: '';
    position: absolute;
    background-color: currentColor;
    width: 100%;
    display: block;
    height: 3px;
    @include sm {
      height: 1px; } }
  &::before {
    top: 0; }
  &::after {
    bottom: 0; }
  span {
    display: block;
    position: relative;
    height: 3px;
    background-color: transparent;
    margin-top: $gap*2;
    margin-bottom: $gap*2;
    @include sm {
      margin-top: $gap-sm*2;
      margin-bottom: $gap-sm*2;
      height: 1px; }
    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 0;
      background-color: currentColor;
      width: 100%;
      display: block;
      height: 3px;
      @include sm {
        height: 1px; } }
    &::before {
      top: $gap;
      @include sm {
        top: $gap-sm; } }
    &::after {
      bottom: $gap;
      @include sm {
        bottom: $gap-sm; } } } }
