.fullpage {
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-color: $black;
  &__wrap {
    position: relative; }
  &__slide {
    @include coverdiv;
    // visibility: hidden
    z-index: 0;
    opacity: 0;
    padding-top: $header-height;
    overflow: hidden;
    @include md {
      padding-top: $header-height-md; }
    @include sm {
      padding-top: $header-height-sm; }
    &.is-active {
      visibility: visible;
      z-index: 1;
      opacity: 1; } } }

.fullpage-section {
  opacity: 0;
  display: none;
  z-index: 0;
  @include coverdiv;
  &.section {
    position: absolute;
    display: none;
    &:last-child {
      padding-bottom: 0; } }
  &.is-active {
    z-index: 1;
    opacity: 1;
    transform: translateY(0%);
    display: flex; }
  &.is-loading {
    display: flex; }
  .ps__rail-y {
    z-index: 5; } }

.fullpage-pagination {
  position: fixed;
  z-index: 15;
  left: 0;
  top: 0;
  bottom: 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  left: 100px;
  display: none;
  padding-top: $header-height;
  @include xl {
    left: 75px; }
  @include md {
    left: 50px;
    padding-top: $header-height-md; }
  @include sm {
    left: 25px;
    padding-top: $header-height-sm; }
  @include rh(900) {
    padding-top: $header-height-sm-vert; }

  &.is-active {
    display: flex;
    @include rh(550) {
      display: none; }
    @include xs {
      display: none; } }
  @include rh(700) {
    a {
      padding-bottom: 20px;
      padding-top: 10px; } }
  li {
    // margin-bottom: 20px
    // margin-top: 20px
    &:first-child {
      display: none; }
    &:last-child {
      a {
        @include before {
          display: none; } } } }
  a {
    color: $gray-dark;
    display: block;
    @include tr(color .3s);
    padding-bottom: 40px;
    padding-top: 20px;
    position: relative;
    font-size: $fz-sm;
    @include before {
      position: absolute;
      height: 20px;
      background-color: currentColor;
      width: 1px;
      left: 50%;
      bottom: 0px;
      transform: translate(-50%, 0); }
    &.is-active {
      color: $white; } } }

