.footer {
  font-size: 16px;
  background-color: $white;
  position: relative;
  z-index: 2;
  // z-index: 15
  // position: fixed
  // left: 0
  // right: 0
  // bottom: 0
  @include sm {
    font-size: 14px; }
  &__inner {
    @include flex(space-between, center, row, wrap);
    // padding-top: 30px
    // padding-bottom: 30px
    height: $footer-height;
    @include lg {
      // padding-top: 15px
      // padding-bottom: 15px
      height: $footer-height-lg; }
    @include md {
      // padding-top: 10px
      // padding-bottom: 10px
      height: $footer-height-md; }
    @include sm {
      justify-content: center; }
    @include xs {
      height: $footer-height-xs;
      padding-top: 10px;
      padding-bottom: 10px; } }
  &__copy {
    color: $gray-dark;
    flex-grow: 1;
    text-align: center;
    @include xs {
      width: 100%;
      padding-top: 10px;
      padding-bottom: 10px; } }
  &__logo {
    width: 150px;
    @include sm {
      width: auto; } }
  &__btn {
    width: 150px;
    text-align: right;
    @include sm {
      width: auto;
      text-align: center; } } }
