.descr {
  color: $white;
  &__title {
    margin-bottom: 150px;
    @include rh(900) {
      margin-bottom: 40px; }
    @include xl {
      margin-bottom: 120px; }
    @include lg {
      margin-bottom: 100px; }
    @include md {
      margin-bottom: 60px; }
    @include sm {
      margin-bottom: 30px !important; }
    @include xs {
      margin-bottom: 10px !important; } }

  &__content {
    @include flex();
    @include sm {
      display: block; } }
  &__block {
    position: relative; } }

.descr-block {
  padding-right: 35px;
  max-width: 485px;
  @include md {
    padding-right: 25px; }
  @include sm {
    padding-right: 0;
    @include notlast {
      margin-bottom: 15px; } }
  &__title {
    margin-bottom: 15px;
    position: relative;
    position: relative;
    padding-left: 35px;
    @include md {
      padding-left: 25px; }
    @include rh(900) {
      margin-bottom: 10px; } }
  &__title-icon {
    position: absolute;
    left: 0;
    top: .4em;
    border-radius: 50%;
    height: 14px;
    width: 14px;
    border: 1px solid currentColor;
    @include inline-flex(center, center);
    vertical-align: middle;
    @include before {
      display: inline-block;
      border-radius: 50%;
      width: 6px;
      height: 6px;
      background-color: currentColor; } }

  &__text {

    padding-left: 35px;
    position: relative;
    @include before {
      position: absolute;
      width: 1px;
      background-color: currentColor;
      top: -.5em;
      bottom: 1.2em;
      left: 6px; }
    @include md {
      padding-left: 25px; } } }

.descr-sm {
  &__content {
    @include flex(flex-start, normal, row, wrap); }
  &__title {
    margin-bottom: 55px;
    @include md {
      margin-bottom: 35px; }
    @include sm {
        margin-bottom: 25px; }
    @include xs {
      margin-bottom: 10px; }
    @include rh(667) {
      margin-bottom: 15px; } } }

.descr-sm-item {
  width: 50%;
  margin-bottom: 35px;
  padding-right: 15px;
  @include md {
    margin-bottom: 25px; }
  @include sm {
    margin-bottom: 15px; }
  @include xs {
    width: 100%; }
  @include rh(667) {
    margin-bottom: 10px; }
  &__title {
    color: $white;
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; }
    @include rh(667) {
      margin-bottom: 5px; } } }

