.features {
  @include flex(center, normal, column);
  text-align: center;
  // padding-left: 15px
  // padding-right: 15px
  // +coverdiv
  width: 100%;
  // min-height: 100vh
  // overflow: hidden
  position: relative;
  z-index: 2;

  &__title {
    margin-bottom: 150px;
    @include lg {
      margin-bottom: 100px; }
    @include md {
      margin-bottom: 50px; }
    @include sm {
      margin-bottom: 20px !important; }
    @include xs {
      margin-bottom: 10px !important; }
    @include rh(900) {
      margin-bottom: 50px; } }
  &__features {
    @include flex(flex-start, normal, row, wrap); } }

.features-bg {
  $this: &;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  @include flex();
  z-index: 0;
  &__col {
    height: 100%;
    width: 33.33333%;
    @include cover;
    &:nth-child(odd) {
      background-color: $black-dark; }
    &:nth-child(even) {
      background-color: $black-darkest; }
    @include sm {
      background-image: none !important; } }

  &--reverse {
    #{$this}__col {
      &:nth-child(even) {
        background-color: $black-dark; }
      &:nth-child(odd) {
        background-color: $black-darkest; } } } }

.feature {
  width: 33.33333%;
  // padding-top: 30%
  padding-left: 15px;
  padding-right: 15px;
  text-align: left;
  @include cover;
  @include xs {
    width: 100%;
    padding-top: 5px;
    padding-bottom: 5px;
    // padding-left: 0
 }    // padding-right: 0
  // &:nth-child(odd)
  //   background-color: $black-dark
  // &:nth-child(even)
  //   background-color: $black-darkest
  &__inner {
    max-width: 355px;
    margin-left: auto;
    margin-right: auto; }
  &__title {
    color: $white;
    position: relative;
    margin-bottom: 15px;
    @include xs {
      margin-bottom: 10px; } }
  &__title-has-icon {
    padding-left: 60px;
    .icon {
      fill: currentColor;
      font-size: 50px;
      position: absolute;
      top: 0;
      left: 0;
      @include sm {
        font-size: 40px; }
      @include xs {
        font-size: 30px; }
      @include rh(900) {
        font-size: 30px; } }
    @include sm {
      padding-left: 50px; }
    @include xs {
      padding-left: 40px; }
    @include rh(900) {
      padding-left: 40px; } }
  &__text {
    color: $gray; }
  &__subttl {
    margin-bottom: 15px; } }

.features-img {
  position: absolute;
  right: 0;
  z-index: 1;
  opacity: 1;
  top: auto;
  left: auto;
  max-height: 100%;

  &--bottom {
    bottom: 0; }
  &--top {
    top: 0;
    bottom: auto !important; } }
