.contacts {
  max-width: 585px;
  &__title {
    margin-bottom: 35px; }
  &__subttl {
    color: $gray-dark;
    margin-bottom: 35px; }
  &__contacts {
    @include flex(flex-start, normal, row, wrap); }
  &__contact {
    width: 50%; }
  &__title-sm {
    width: 100%;
    margin-bottom: 10px; } }

.contact {
  margin-bottom: 10px;
  &__title {
    color: $gray-dark; }
  &__link {
    color: $white;
    @include hover {
      text-decoration: underline; } } }
